            } else {
                this.openList = true;
            }
        },
        add: function() {
            console.log(this.list2);
        },
        replace: function() {
            this.list = [{ name: "Edgard" }];
        },
        clone: function(el) {
            return {
                name: el.name + " cloned"
            }
        },
        log: function(evt) {
            window.console.log(evt);
        }
